import { z } from "zod";

export enum CursorEventType {
  MOVE = "move",
  REMOVE = "remove",
  CLICK = "click",
}

const CursorEventMoveZod = z.object({
  type: z.literal(CursorEventType.MOVE),
  rX: z.number(),
  rY: z.number(),
});
export type CursorEventMove = z.infer<typeof CursorEventMoveZod>;

const CursorEventRemoveZod = z.object({
  type: z.literal(CursorEventType.REMOVE),
});
type CursorEventRemove = z.infer<typeof CursorEventRemoveZod>;

const CursorEventClickZod = z.object({
  type: z.literal(CursorEventType.CLICK),
  rX: z.number(),
  rY: z.number(),
});
type CursorEventClick = z.infer<typeof CursorEventClickZod>;

export const CursorEventZod = z.union([
  CursorEventMoveZod,
  CursorEventRemoveZod,
  CursorEventClickZod,
]);
export type CursorEvent = z.infer<typeof CursorEventZod>;

export type CursorResp = {
  connId: string;
  event: CursorEvent;
};
